.company-li{
    padding:5px;
    justify-content: space-between;
    flex-direction: row;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
}
.company-ul{
    padding: 0;
    width: 360px;
    display: block;
}

.inner-company-ul{
    column-count: 2;
    padding: 2px;
    margin-bottom: 5px;
}
.inner-company-ul:hover{
    column-count: 2;
    padding: 2px;
    margin-bottom: 5px;
    background-color: #ddd;
}
.inner-company-ul button{
    border : None;
    display: None;
    outline: None;
} 
.inner-company-ul:hover button{
    display: block;
    background-color: #ddd;
}


.inner-company-ul button:active{
    border: none;
} 