.outer-ul{
    column-count: 3;
    margin-right: auto;
    border: 1px solid #eee;
    padding: 8px;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
    width: 980px;
    background-color: #eee;
    border-radius: 1%;
}
.inner-ul{
    column-count: 2;
}
.description{
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    margin-bottom: 20px;
    margin-top: 20px;
    background-color: #eee;
    border: 1px solid #eee;
    padding: 8px;
}

.bold-list{
    font-weight: 400;
}

