.header-ul{
    column-count: 2;
    padding: 8px;
    text-align: center;
}
.inner-header{
    margin: 0;
}

.header-ul a{
    color: black;
    text-decoration: None;
    line-height: 20px;
    font-size: 25px;
}

.header-ul a:hover{
    font-size: 28px;
    color: white;
    background-color: black;
    border-radius: 5%;
    padding: 1px;
}

